import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AlertModule } from 'ngx-bootstrap/alert';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { SnotifyModule } from 'ng-snotify';
import { HttpModule } from '@angular/http';
import { AuthenticationModule, EnvironmentModule, FormValidationModule, NotificationModule, LocalDataModule } from '@app/shared';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
import { FileUploadModule } from 'ng2-file-upload';
import { TagInputModule } from 'ngx-chips';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { defineLocale } from 'ngx-bootstrap/chronos';
import { esLocale } from 'ngx-bootstrap/locale';
import { ComponentesModule } from '@app/componentes/componentes.module';
import { FomulariodinamicoRoutingModule } from './fomulariodinamico-routing.module';
import { FomulariodinamicoIndexComponent } from './fomulariodinamico-index/fomulariodinamico-index.component';
import { FomulariodinamicoAddComponent } from './fomulariodinamico-add/fomulariodinamico-add.component';
import { DragulaModule } from 'ng2-dragula';
defineLocale('es', esLocale);
@NgModule({
  imports: [
    CommonModule,
    FomulariodinamicoRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    // Ng4LoadingSpinnerModule.forRoot(),
    AlertModule.forRoot(),
    SweetAlert2Module.forRoot(),
    SnotifyModule,
    HttpModule,
    AuthenticationModule,
    EnvironmentModule,
    FormValidationModule,
    NotificationModule,
    LocalDataModule,
    TypeaheadModule.forRoot(),
    FileUploadModule,
    TagInputModule,
    BsDatepickerModule.forRoot(),
    ComponentesModule,
    DragulaModule.forRoot(),
  ],
  declarations: [FomulariodinamicoIndexComponent, FomulariodinamicoAddComponent]
})
export class FomulariodinamicoModule { }



