import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FomulariodinamicoIndexComponent } from './fomulariodinamico-index/fomulariodinamico-index.component';
import { FomulariodinamicoAddComponent } from './fomulariodinamico-add/fomulariodinamico-add.component';
import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service'; 
const routes: Routes = [ { path: '', component: FomulariodinamicoIndexComponent},
{ path: 'add', component: FomulariodinamicoAddComponent, canDeactivate: [Candeactivateguard]},
{ path: 'edit/:id', component: FomulariodinamicoAddComponent, canDeactivate: [Candeactivateguard]}];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FomulariodinamicoRoutingModule { }



