import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from '@app/shared';
import { LocalDataService } from '@app/shared';
import { Candeactivateguard } from '@app/shared/can-deactivate-guard/can-deactivate-guard.service';
import { FomulariodinamicoService } from '@app/services/fomulariodinamico/fomulariodinamico.service';
import { FomulariosDinamico } from '@app/viewmodel/formulariodinamico';
import { ElementoCampo } from '@app/viewmodel/elementocampo';
import { DragulaService } from 'ng2-dragula';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
declare var $;

@Component({
  selector: 'app-fomulariodinamico-add',
  templateUrl: './fomulariodinamico-add.component.html',
  styleUrls: ['./fomulariodinamico-add.component.css',
    '../../../../../../node_modules/dropify/dist/css/dropify.css']
})
export class FomulariodinamicoAddComponent implements OnInit {
  @ViewChild('configurarSwal') private configurarSwal: SwalComponent;
  item: FomulariosDinamico;
  id: string;
  elementosbase: ElementoCampo[];
  ctrSel: any;
  @HostListener('window:scroll', ['$event']) onScrollEvent($event) {
    let top = document.documentElement.scrollTop;
    let topmenu: any = $('#left');
    if (top > 800) {
      topmenu.css({ "position": "fixed", "top": "100px", "width": "233.25px" });
    } else {

      topmenu.removeAttr("style");
    }


  }
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private fomulariodinamicoService: FomulariodinamicoService,
    private router: Router,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private candeactivateguard: Candeactivateguard,
    private route: ActivatedRoute,
    private dragulaService: DragulaService,
  ) {
    this.candeactivateguard.setedit(true);
    this.item = new FomulariosDinamico();
    this.crearElementosBase();
    this.dragulaService.destroy('CONTROLES');
    this.dragulaService.createGroup('CONTROLES', {
      copy: (el, source) => {
        return source.id === 'left';
      },
      copyItem: (elementoCampo: any) => {
        let newcontrol = new ElementoCampo();

        newcontrol = Object.assign({}, elementoCampo);
        newcontrol.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
        return newcontrol;
      },
      accepts: (el, target, source, sibling) => {
        // To avoid dragging from right to left container
        return target.id !== 'left';
      }
    });
  }
  crearElementosBase() {
    this.elementosbase = [];
    let elemento = new ElementoCampo();
    elemento.tipo = 'Checkbox';
    elemento.nombre = 'Checkbox';
    elemento.requerido = false;
    elemento.archivos = false;

    this.elementosbase.push(elemento);
    elemento = new ElementoCampo();
    elemento.tipo = 'Lista';
    elemento.nombre = 'Lista';
    elemento.valoreslista = "";
    elemento.requerido = false;
    elemento.archivos = false;

    this.elementosbase.push(elemento);
    elemento = new ElementoCampo();
    elemento.tipo = 'Numerico';
    elemento.dato = 'number';
    elemento.nombre = 'Numerico';
    elemento.requerido = false;
    elemento.archivos = false;

    this.elementosbase.push(elemento);
    elemento = new ElementoCampo();
    elemento.tipo = 'Texto';
    elemento.dato = 'text';
    elemento.nombre = 'Texto';
    elemento.requerido = false;
    elemento.archivos = false;

    this.elementosbase.push(elemento);
    elemento = new ElementoCampo();
    elemento.tipo = 'Date';
    elemento.dato = 'date';
    elemento.nombre = 'Fecha';
    elemento.requerido = false;
    elemento.archivos = false;
    this.elementosbase.push(elemento);

    elemento = new ElementoCampo();
    elemento.tipo = 'Split';
    elemento.nombre = 'Separador';
    elemento.requerido = false;
    elemento.archivos = false;
    this.elementosbase.push(elemento);
  }

  ngOnInit() {
    this.id = this.route.snapshot.paramMap.get('id');
    if (this.id == undefined) {
      this.item = new FomulariosDinamico();
      this.item.controles = [];
    } else {
      this.getById();
    }

  }
  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.fomulariodinamicoService.getById($id)
      .subscribe((response) => {
        this.item = response.data;
        if (this.item.controles == undefined) {
          this.item.controles = [];
        }
        this.spinnerService.hide();
      })
  }

  add() {
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.fk_empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    if (this.id == undefined) {
      this.fomulariodinamicoService.add(this.item)
        .subscribe((value) => {
          this.spinnerService.hide();
          this.notificationService.onSuccess('Formulario agregado.');
          this.router.navigateByUrl('obra/formulariodinamico');
        }, (err) => {
          this.spinnerService.hide();
          if (err.status_code == 422) {
            this.notificationService.onError("El formulario ya existe");
          } else { this.notificationService.onError("Se ha producido un error."); }
        });
    } else {
      this.fomulariodinamicoService.update(this.item)
        .subscribe((value) => {
          this.spinnerService.hide();
          this.notificationService.onSuccess('Formulario actualizado con exito.');
          this.router.navigateByUrl('obra/formulariodinamico');
        }, (err) => {
          this.spinnerService.hide();
          if (err.status_code == 422) {
            this.notificationService.onError("El formulario ya existe");
          } else { this.notificationService.onError("Se ha producido un error."); }
        });
    }

  }
  abrirmodal(elemento) {
    this.ctrSel = elemento;
    this.configurarSwal.show();
    return false;
  }

  eliminarElemento(elemento) {
    let index = this.item.controles.indexOf(elemento);
    this.item.controles.splice(index, 1);
  }

}



